<template>
  <div class="logistics">
    <div class="logistics-item logistics-top flex ac">
      <CustomImg class="goods-img" width="1.6rem" height="1.6rem" fit='contain' :src="logo"></CustomImg>
      <div class="item logistics-top-center">
        <div>{{shipping_name}}</div>
        <div>快递单号：{{shipping_code}}</div>
      </div>
      <div class="copy flex ac center" :data-clipboard-text="shipping_code" @click="copy">
        <img :src="require('@/assets/copy.png')" alt /> 复制
      </div>
    </div>
    <div class="logistics-item logistics-center flex ac">
      <img :src="require('@/assets/address.png')" alt />
      <div class="item">
        <div>订单编号：{{order_sn}}</div>
        <div>
          收货地址：{{open ? address : `${address.slice(0, 12)}...`}}
          <span
            v-if="!open"
            @click="open = true"
          >展开</span>
        </div>
      </div>
    </div>
    <div class="logistics-item logistics-bottom">
      <van-steps :active="active" direction="vertical" active-color="#7ED321">
        <van-step v-for="(item, index) in log" :key="index">
          <div v-html="item.context" class="desc" :class="{active: index === 0 && shpping_status === '已签收'}"></div>
          <div class="time" :class="{active: index === 0 && shpping_status === '已签收'}">{{item.time}}</div>
          <img
            v-if="shpping_status === '已签收'"
            class="car"
            slot="active-icon"
            :src="require('@/assets/ok.png')"
            alt
          />
          <img v-else class="ok" slot="active-icon" :src="require('@/assets/car.png')" alt />
          <div class="icon" slot="inactive-icon"></div>
        </van-step>
      </van-steps>
    </div>
  </div>
</template>

<script>
import ClipboardJS from "clipboard";
import { getLogisticsInfo } from "@/api";
import Vue from "vue";
import { Step, Steps } from "vant";

Vue.use(Step);
Vue.use(Steps);

export default {
  data() {
    return {
      address: "",
      open: true,
      shipping_code: "",
      shipping_name: "",
      shpping_status: "",
      order_sn: "",
      log: [],
      active: 0,
      logo: ""
    };
  },
  created() {
    this.getData();
  },
  methods: {
    // 数据
    getData() {
      const { order_id, order_sn } = this.$route.query;
      this.order_sn = order_sn;
      getLogisticsInfo({ order_id }).then(res => {
        if (res.success) {
          const {
            shipping_code,
            shipping_name,
            shpping_status,
            log,
            logo,
            address
          } = res.data;
          this.shipping_code = shipping_code;
          this.shipping_name = shipping_name;
          this.shpping_status = shpping_status;
          this.log = log.map(item =>{
            let context = item.context
            const mobiles = context.match(/\d{11}/g) ? context.match(/\d{11}/g) : []
            mobiles.forEach(mobile => {
              context = context.replace(mobile, `<a href="tel:${mobile}" style='color:#4A90E2'>${mobile}</a>`)
            })
            return {
              ...item,
              context
            }
          });
          this.logo = logo;
          this.address = address;
          this.open = address ? address.length < 12 : true;
        }
      });
    },
    // 复制
    copy() {
      const clipboard = new ClipboardJS(".copy");
      clipboard.on("success", e => {
        this.$toast("复制成功");
        e.clearSelection();
      });
      clipboard.on("error", () => {
        this.$toast("复制失败");
      });
    }
  }
};
</script>

<style lang="less" scoped>
.logistics {
  padding: 0 13px;
  overflow: hidden;
  .van-step--vertical:not(:last-child)::after {
    border-bottom: none;
  }
  textarea {
    position: fixed;
    bottom: -100vh;
    left: 0;
  }
}

.logistics-item {
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 4px 0px rgba(193, 193, 193, 0.5);
  border-radius: 6px;
  margin: 10px 0;
}

.logistics-top {
  padding: 10px 0;
}

.goods-img {
  margin: 0 10px;
}

.logistics-top-center {
  font-size: 14px;
  line-height: 20px;
  div:first-child {
    color: #7ed321;
  }
  div:last-child {
    color: #666;
    margin-top: 5px;
  }
}

.logistics-bottom {
  padding: 10px 13px;
}

.copy {
  width: 50px;
  height: 23px;
  background: rgba(252, 149, 0, 1);
  border-radius: 12px 0px 0px 12px;
  box-sizing: border-box;
  color: #fff;
  font-size: 12px;
  img {
    width: 13px;
    margin-right: 5px;
  }
}

.logistics-center {
  color: #666;
  font-size: 14px;
  line-height: 20px;
  padding: 5px 0;
  overflow: hidden;
  img {
    width: 21px;
    margin: 0 13px;
  }
  .item div {
    margin: 5px 0;
    padding-right: 13px;
    span {
      color: #979797;
    }
  }
}

.desc {
  color: #888;
  font-size: 13px;
  line-height: 19px;
}

.time {
  color: #888;
  font-size: 11px;
  line-height: 15px;
}

.car {
  width: 20px;
}

.ok {
  width: 15px;
}

.active {
  color: #7ed321;
}

.icon {
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #d8d8d8;
  border-radius: 50%;
}
</style>




